import React from "react";

import 'moment/locale/pt-br';
import Link from "gatsby-link";
import Helmet from "react-helmet";
import moment from 'moment-timezone';
import { LocaleProvider } from 'antd';

moment.locale('pt-br');
import ptBR from 'antd/lib/locale-provider/pt_BR';

import favicon from '../img/favicon.png';

const ListLink = props =>
    <li style={{ display: `inline-block`, marginRight: `1rem` }}>
        <Link to={props.to}>
            {props.children}
        </Link>
    </li>

export default ({ children, data }) => (
    <LocaleProvider locale={ptBR}>
        <div style={{ margin: `0 auto`, maxWidth: 960, padding: `1.25rem 1rem` }}>
            <Helmet>
                <title>{data.site.siteMetadata.title}</title>
                <script async src="https://www.googletagmanager.com/gtag/js?id=UA-61529437-3"></script>
                <script>{`
                    window.dataLayer = window.dataLayer || [];
                    function gtag(){dataLayer.push(arguments);}
                    gtag('js', new Date());
                  
                    gtag('config', 'UA-61529437-3');

                `}</script>
                <meta charSet="utf8" />
                <meta name="description" content="Minha página pessoal" />
                <meta name="robots" content="all" />
                <meta name="author" content="Marllon Frizzo" />
                <meta name="viewport" content="width=device-width, initial-scale=1.0" />
                <link rel="icon" href={favicon} />
            </Helmet>

            <header style={{ marginBottom: `1.5rem` }}>
                <Link to="/" style={{ textShadow: `none`, backgroundImage: `none` }}>
                    <h3 style={{ display: `inline` }}>Marllon Frizzo</h3>
                </Link>
                <ul style={{ listStyle: `none`, float: `right` }}>
                    <ListLink to="/">Início</ListLink>
                    <ListLink to="/projetos/">Projetos</ListLink>
                    <ListLink to="/sobre/">Sobre</ListLink>
                </ul>
            </header>
            {children()}
        </div>
    </LocaleProvider>
);

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`



